import { connect } from 'react-redux';
import React, { Component } from 'react';
import { Todo, nextStatus } from './Todo';
import { KINTO } from './Constants';
import './App.css';

class App extends Component {
  constructor(props) {
    super(props)
    this.inputRef = React.createRef();
    this._maybeNew = this.maybeNew.bind(this);
    this._addTodo = this.addTodo.bind(this);
  }
  componentWillMount() {
    const h = new Headers();
    h.set("Authorization", this.props.authorization);
    fetch(KINTO+"/buckets/default", {headers:h})
      .then(resp=>resp.json())
      .catch(err=>{
        console.log("ERR", err);
      }).then(json=>{
        const bid = json.data.id;
        const uri = KINTO+"/buckets/"+bid+"/collections";
        fetch(uri, {headers:h})
          .then(resp=>resp.json())
          .catch(err=>{
            console.log("ERR2", err);
          })
          .then(json=>{
            console.log("COLLECTIONS", bid, json);
            this.getOrCreate(json.data, bid, h);
          });
      });
  }
  getOrCreate(collections, bid, h) {
    h.set("Content-Type", "application/json");
    if(collections.length && collections
        .filter(c=>"todolists" === c.id).length) {
      console.log("LISTING", bid);
      this.listTodos(bid);
    } else {
      const uri = KINTO+"/buckets/"+bid+"/collections";
      fetch(uri, {method:"POST", headers:h, body:JSON.stringify({
          data:{id:"todolists"}
        })})
        .then(r=>r.json())
        .then(j=>{
          console.log("CREATED", j);
          this.listTodos(bid);
        });
    }
  }
  listTodos(bid) {
    const h = new Headers();
    h.set("Authorization", this.props.authorization);
    const uri = KINTO+"/buckets/"+bid+"/collections/todolists/records";
    fetch(uri, {headers:h})
        .then(r=>r.json())
        .catch(err=>{
            console.log("ERR3", err);
        })
        .then(j=>{
          console.log("GOT LISTS", j);
          this.createList(bid, j);
        });
  }
  createList(bid, j) {
    if(j.data.length) {
      const l = j.data[0];
      const server = [KINTO, "/buckets/", bid,
          "/collections/todolists/records/", l.id].join("");
      this.props.onReceivedServerList(l.todos, server);
    } else {
      const baseurl = [KINTO, "/buckets/", bid,
          "/collections/todolists/records"].join("");
      const h = new Headers();
      h.set("Authorization", this.props.authorization);
      h.set("Content-Type", "application/json");
      fetch(baseurl, {method:"POST", headers:h, body:JSON.stringify({
          data:{todos:[]}
        })})
        .then(r=>r.json())
        .then(j=>{
          console.log("CREATED LIST", j);
          this.props.onReceivedServerList([], baseurl+"/"+j.data.id);
        });
    
    }
  }
  maybeNew(event) {
    if(event.keyCode === 13) {
      this.addTodo();
    }
  }
  addTodo() {
    const title = this.inputRef.current.value;
    if(title) {
      this.props.onNewTodo(title);
      this.inputRef.current.value = "";
    }
  }
  render() {
    return (
      <main>
      <h1>Todo list</h1>
      <ul className="todolist">
      {this.props.todos.map(todo=>{
        const _statusChange = this.props.onStatusChange.bind(
              this,todo.id,nextStatus(todo.status));
        const _titleChange = this.props.onTitleChange.bind(
              this,todo.id);
        return (
            <li key={todo.id}>
            <Todo id={todo.id} title={todo.title}
              status={todo.status} creation={todo.creation}
              onStatusChange={_statusChange}
              onTitleChange={_titleChange}
            />
            </li>
          );
      })}
      </ul>
      <div className="newtitle">
        <input type="text" onKeyUp={this._maybeNew} 
          ref={this.inputRef} />
        <button onClick={this._addTodo}>Add</button>
      </div>
      </main>
    );
  }
}

export default connect(
    state=>({todos:state.todos, authorization:state.authorization}),
    dispatch=>({
      onStatusChange: (id,status)=>{
        dispatch({type:"STATUS_CHANGE", id:id,status:status});
      },
      onTitleChange: (id, title) => {
        if(title.trim()) {
          dispatch({type:"TITLE_CHANGE", id:id,title:title});
        } else {
          dispatch({type:"TODO_DELETE", id:id});
        }
      },
      onNewTodo: (title) => {
        dispatch({type:"TODO_ADD", title:title});
      },
      onReceivedServerList: (todos, url) => {
        console.log("RSL", todos);
        dispatch({type:"REPLACE_ALL", 
            todos:todos,
            syncurl:url,
          });
      },
    }),
)(App);

