import React, { Component } from 'react';
import './Todo.css';

const STATUSES = "todo,ongoing,finished,removed,canceled".split(/,/g);
function nextStatus(s) {
    return STATUSES[(1+STATUSES.indexOf(s || ""))%(STATUSES.length)]
}
function getStatusAction(id, newStatus) {
    return {type:"STATUS_CHANGE", id:id, status:newStatus};
}
function getTitleAction(id, newTitle) {
    return {type:"TITLE_CHANGE", id:id, title:newTitle};
}
function getAddAction(title) {
    return {type:"TODO_ADD", title:title};
}

class Todo extends Component {
  constructor(props) {
    super(props);
    this.state = {
      editing: false,
    }
    this.inputRef = React.createRef();
    this._onEditionStart = this.onEditionStart.bind(this);
    this._onEditionEnd = this.onEditionEnd.bind(this);
  }
  onEditionStart(e) {
    if(!(this.state.editing)) {
        this.setState({editing:true});
    }
  }
  onEditionEnd(e) {
    if(this.state.editing) {
      this.setState({editing:false});
      const newTitle = this.inputRef.current.innerText;
      this.props.onTitleChange && this.props.onTitleChange(newTitle);
    }
  }
  componentDidUpdate() {
    if(this.state.editing) {
      this.inputRef.current.focus();
    }
  }
  render() {
    return (
      <div className={"task "+(this.props.status || "todo")}>
        <button onClick={this.props.onStatusChange} />
        <span contentEditable={this.state.editing}
          onDoubleClick={this._onEditionStart}
          onBlur={this._onEditionEnd}
          ref={this.inputRef}
          >{this.props.title}</span>
        <time>{this.props.creation}</time>
      </div>
    );
  }
}

export {Todo,nextStatus,getStatusAction,getTitleAction,getAddAction};

