import { connect } from 'react-redux';
import { KINTO } from './Constants';
import React, { Component } from 'react';
import './CredentialsProvider.css';

class CredentialsProvider extends Component {
  loginRef = React.createRef();
  passwordRef = React.createRef();
  submitted = event => {
    event.preventDefault();
    const login = this.loginRef.current.value;
    const password = this.passwordRef.current.value;
    if(login && password) {
      const h = new Headers();
      const auth = "Basic "+btoa(login+":"+password);
      h.set("Authorization", auth);
      fetch(KINTO+"/", {headers:h})
        .then(r=>r.json())
        .then(json=>{
          if(json.user) {
            this.props.onAuthOk(auth);
          }
        });
    }
  }
  render() {
    if(this.props.authorization) {
      return (<div>{this.props.children}</div>);
    } else {
      return (
        <form className="login-component" onSubmit={this.submitted}>
          <div className="login-flex">
            <label>Login</label><input ref={this.loginRef} type="text" />
            <label>Password</label><input ref={this.passwordRef} 
              type="password" />
            <input type="submit" value="Login" />
          </div>
        </form>
        );
    }
  }
}

export default connect(
  state=>({authorization:state.authorization}),
  dispatch=>({
    onAuthOk:auth=>dispatch({type:"AUTH_OK", authorization:auth}),
  }),
)(CredentialsProvider);
