import React from 'react';
import ReactDOM from 'react-dom';
import * as serviceWorker from './serviceWorker';
import { createStore } from 'redux';
import { Provider } from 'react-redux';
import CredentialsProvider from './CredentialsProvider';
import App from './App';
import './index.css';

function reducer(state, action) {
    switch(action.type) {
        case "STATUS_CHANGE":
            return {...state,
                todos:state.todos.map(t=>{
                    if(t.id !== action.id) return t;
                    return {...t, status:action.status, };
                })
            };
        case "TITLE_CHANGE":
            return {...state,
                todos:state.todos.map(t=>{
                    if(t.id !== action.id) return t;
                    return {...t, title:action.title, };
                })
            };
        case "TODO_ADD":
            const maxid = state.todos.reduce((acc,t)=>Math.max(acc,t.id), 0);
            return {...state,
                todos:state.todos.concat([{
                    id:maxid+1,
                    status:"todo",
                    title:action.title,
                    creation:(new Date()).toISOString().replace(/T.*/,""),
                }])
            };
        case "TODO_DELETE":
            return {...state,
                todos:state.todos.filter(t=>t.id !== action.id),
            };
        case "AUTH_OK":
            return {...state,
                authorization:action.authorization,
            };
        case "REPLACE_ALL":
            console.log("GOTCHA", action);
            return {...state,
                todos:action.todos,
                syncurl:action.syncurl,
            };
        default:
            return state;
    }
}

const store = createStore(reducer, {todos:[]});
store.subscribe(() => console.log("STORE", JSON.stringify(store.getState())));
store.subscribe(() => {
  const state = store.getState();
  if(state.syncurl) {
    const payload = JSON.stringify({ data: {
      todos:state.todos } });
    const h = new Headers();
    h.set("Authorization", state.authorization);
    h.set("Content-Type", "application/json");
    fetch(state.syncurl, {headers:h, method:"PUT", body:payload})
      .then(resp=>resp.json())
      .then(j=>{
        console.log("SAVED", j);
      });
  }
});

//store.dispatch({type:"TODO_ADD",title:"First one"});
//store.dispatch({type:"TITLE_CHANGE",id:1,title:"The first one"});
//store.dispatch({type:"TODO_ADD",title:"Second one"});
//store.dispatch({type:"STATUS_CHANGE",id:1,status:"finished"});
//store.dispatch({type:"TODO_DELETE",id:1});
//store.dispatch({type:"TODO_DELETE",id:2});

ReactDOM.render(
     (<Provider store={store}>
       <CredentialsProvider>
        <App />
       </CredentialsProvider>
      </Provider>),
    document.getElementById('root')
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: http://bit.ly/CRA-PWA
serviceWorker.unregister();
